.hero__conteiner {
  @media screen and (max-width: 767.98px) {
    text-align: center;
    padding-bottom: 10px;
  }

  @media screen and (min-width: 768px) {
    padding-bottom: 80px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 57px;
  }

  & .button:hover,
  & .button:focus {
    color: var(--secondary-bg-color);
    transition: color var(--duration) var(--cubic) var(--delay);
  }
}

.hero__title {
  width: 224px;
  margin-bottom: 3px;
  font-family: var(--decorative-font);
  font-size: 30px;
  line-height: 1.27;

  @media screen and (max-width: 767.98px) {
    position: relative;
    margin: auto;
  }
  @media screen and (min-width: 1280px) {
    width: 490px;
    margin-bottom: 15px;
    font-size: 64px;
    line-height: 1.02;
  }
}

.hero__text {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  @media screen and (max-width: 767.98px) {
    margin-bottom: 36px;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 26px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 24px;
    line-height: 1.21;
    margin-bottom: 30px;
  }
}
